/* Шрифты */

@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,600;1,400;1,600&display=swap');

/* Переменные */
$gold: #fbbe18;
$dark: #20252a;
$white: #fafafa;
$montserat: 'Montserrat', sans-serif;
$transition: all 0.2s linear; 

.container{
	max-width: 100%!important;
	width: 100%;
	padding: 0 15px;
	.row{
		padding: 0;
		margin: 0 auto;
	}
}

.btn-black{
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	background: transparent;
	border: 2px solid $dark;
	color: $dark;
	transition: $transition;
	border-radius: 30px;
	padding: 10px 15px;
	font-weight: 600;
	width: 240px;
	&:hover{
		transition: $transition;
		background: $dark;
		color: #ffffff;
	}
}

.btn-gold{
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	background: transparent;
	border: 2px solid $gold;
	color: #ffffff;
	transition: $transition;
	border-radius: 30px;
	padding: 10px 15px;
	font-weight: 600;
	width: 240px;
	&:hover{
		transition: $transition;
		background: $gold;
		color: $dark;
	}
}

*{
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}
html,body{
	min-height: 100%;
	position: relative;
	font-family: $montserat;
	background: $dark;
}

::-webkit-scrollbar {
    width: 12px;
    background-color: $dark;
}
::-webkit-scrollbar-thumb {
    background-color: $gold;
}


img{
	width: 100%;
	height: auto;
}

::selection {
  background: $gold;
  color: $dark;
}



h1,h2,h3,h4,h5,h6{
	color: $gold;
}

a{
	text-decoration: none;
	transition: $transition;
	&:hover{
		transition: $transition;
	}
}

.content-block{
	padding: 120px 0;
	@media screen and (max-width: 991px){
		padding: 60px 0;
	}
}

.header-block{
	display: flex;
	flex-direction: column;
	padding-bottom: 80px;
	position: relative;
	opacity: 0;
	h3{
		font-size: 36px;
		margin-bottom: 35px;
		font-weight: 600;
	}
	span{
		display: block;
	}
	@media screen and (max-width: 991px){
		padding-bottom: 40px;
		h3{
			font-size: 24px;
		}
	}
}

.out{
	background: $gold;
}

.show{
	display: flex!important;
	transition: all 1s linear 0s;
}

header{
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	margin: 0 auto;
	z-index: 100;
	background: rgba($gold, 1);
	.container{
		display: flex;
    	height: 100%;
    	padding: 0;
	}
	.header{
		display: flex;
		justify-content: space-between;
		width: 100%;
		height: 100%;
		margin: 0 auto;
		.burger-wrapper{
			display: none;
			cursor: pointer;
		}
		.logo{
			display: flex;
    		align-items: center;
			img{
				max-width: 230px;
				width: 100%;
				height: auto;
			}
		}
		.nav{
			list-style: none;
			display: flex;
			align-items: center;
			justify-content: flex-end;
			transition: all 1s linear 0s;
			li{
				height: 100%;
				a{
					height: 100%;
				    display: flex;
			    	align-items: center;
					font-size: 14px;
					font-weight: 600;
					color: $dark;
					padding: 0 15px;
					&:hover{
						background: $dark;
						color: $gold;
					}
				}
			}
		}
	}
	@media screen and (max-width: 991px){
		.header{
			.burger-wrapper{
				display: flex;
				align-items: center;
			    justify-content: flex-end;
			    .burger-item{
			    	height: 30px;
				    width: 30px;
				    display: flex;
				    align-items: center;
					span{
						display: block;
						width: 30px;
						height: 4px;
						background: $dark;
						position: relative;
						&:after{
							content: "";
							position: absolute;
							top: 0;
							right: 0;
							left: 0;
							margin: 0 auto;
							width: 30px;
							height: 4px;
							background: $dark;
							margin-top: 10px;
						}
						&:before{
							content: "";
							position: absolute;
							top: 0;
							right: 0;
							left: 0;
							margin: 0 auto;
							width: 30px;
							height: 4px;
							background: $dark;
							margin-top: -10px;
						}
					}
			    }
			}
			.nav{
				display: none;
			    justify-content: center;
			    padding: 0;
			    transition: all 1s linear 0s;
			    height: 60px;
    			li{
					height: 100%;
					a{
						height: 100%;
					    display: flex;
				    	align-items: center;
						font-size: 14px;
						font-weight: 600;
						color: $dark;
						padding: 0 15px;
						&:hover{
							background: $dark;
							color: $gold;
						}
					}
				}
			}
		}
	}
	@media screen and (max-width: 768px){
		.header{
			.nav{
				width: 100%;
				height: 100%;
				li{
					width: 100%;
					height: 60px;
					a{
						width: 100%;
						height: 100%;
					}
				}
			}
		}
	}
}
.content{
	.slider{
		margin: 0 auto;
		height: 100vh;
		.slick-list{
			height: 100%!important;
		}
		.slick-track{
			height: 100%!important;
		}
		&_item{
			position: relative;
			background-size: cover!important;
			background-position: center!important;
			background-repeat: no-repeat!important;
			height: 100%!important;
			&:after{
				content: '';
				width: 100%;
				height: 100%;
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				margin: 0 auto;
				background: rgba($dark,.6);
			}
			.container{
				height: 100%;
			}
		}
		&_text{
			display: flex;
		    flex-direction: column;
		    justify-content: center;
		    position: relative;
		    z-index: 10;
		    height: 100%;
		    color: #fafafa;
		    font-size: 24px;
		    margin: 0 auto;
		    padding: 30px;
		    h3{
		    	font-size: 55px;
		    	font-weight: 600;
		    	color: $gold;
		    	text-transform: uppercase;
		    	font-style: italic;
		    	margin-bottom: 45px;
		    }
		    @media screen and (max-width: 991px){
		    	font-size: 18px;
		    	h3{
		    		font-size: 36px;
		    		margin-bottom: 20px;
		    	}
		    }
		    @media screen and (max-width: 768px){
		    	font-size: 16px;
		    	padding: 0;
		    	h3{
		    		font-size: 24px;
		    		margin-bottom: 20px;
		    	}
		    }
		}
		.slick-prev{
		    position: absolute;
			top: calc(50% - 18px);
			left: 0;
			z-index: 20;
			background: url(../img/icon/arrow.svg) no-repeat;
			background-size: contain;
			transform: scale(-1);
			width: 40px;
			height: 40px;
			transition: $transition;
			&:before{
				content: "";
			}
			&:hover{
				transition: $transition;
				transform: scale(-1);
				background: url(../img/icon/arrow-gold.svg) no-repeat;
				position: absolute;
				top: calc(50% - 18px);
				right: 0;
				z-index: 20;
				background-size: contain;
				width: 40px;
				height: 40px;
			}
		}
		.slick-next{
			position: absolute;
			top: 50%;
			right: 0;
			z-index: 20;
			background: url(../img/icon/arrow.svg) no-repeat;
			background-size: contain;
			width: 40px;
			height: 40px;
			transition: $transition;
			&:before{
				content: "";
			}
			&:hover{
				transition: $transition;
				background: url(../img/icon/arrow-gold.svg) no-repeat;
			    top: 50%;
				right: 0;
				z-index: 20;
				background-size: contain;
				width: 40px;
				height: 40px;
			}
		}
	    @media screen and (max-width: 991px){
	    	&_text{
			    h3{
					text-align: center;
			    }
			    span{
			    	text-align: center;
			    	width: 100%;
			    }
			}
	    }
	}
	.about_us{
		color: $white;
		&.content-block{
			padding-bottom: 0;
		}
		.staff{
			align-items: center;
			padding-bottom: 40px;
		}
		.staff-slider{
			position: relative;
			margin-bottom: 120px;
			&_item{
				display: flex;
				flex-direction: column;
				align-items: center;
				text-align: center;
				margin: 0 20px;
				height: 360px;
				&__img{
					img{
						height: 240px;
						width: 100%;
						object-fit: cover;
					}
				}
				.position{
					display: block;
					margin: 15px 0 5px 0;
				}
				.FIO{
					display: block;
					color: $gold;
				}
			}
		.slick-prev{
		    position: absolute;
			top: calc(50% - 18px);
			left: 0;
			z-index: 20;
			background: url(../img/icon/arrow.svg) no-repeat;
			background-size: contain;
			transform: scale(-1);
			width: 40px;
			height: 40px;
			transition: $transition;
			&:before{
				content: "";
			}
			&:hover{
				transition: $transition;
				transform: scale(-1);
				background: url(../img/icon/arrow-gold.svg) no-repeat;
				position: absolute;
				top: calc(50% - 18px);
				right: 0;
				z-index: 20;
				background-size: contain;
				width: 40px;
				height: 40px;
			}
		}
		.slick-next{
			position: absolute;
			top: 50%;
			right: 0;
			z-index: 20;
			background: url(../img/icon/arrow.svg) no-repeat;
			background-size: contain;
			width: 40px;
			height: 40px;
			transition: $transition;
			&:before{
				content: "";
			}
			&:hover{
				transition: $transition;
				background: url(../img/icon/arrow-gold.svg) no-repeat;
			    top: 50%;
				right: 0;
				z-index: 20;
				background-size: contain;
				width: 40px;
				height: 40px;
			}
		}
		}
	}
	.info{
		align-items: center;
		justify-content: space-between;
		height: inherit;
		&-wrapper{
			position: relative;
			padding: 40px 0;
			background: url(../img/bg.jpg) no-repeat;
			background-position: center;
			background-size: cover;
			&:after{
					content: "";
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					margin: 0 auto;
					height: 100%;
					width: 100%;
					background: rgba($gold, .7);
			}
			.container{
				position: relative;
				z-index: 40;
				height: 100%;
			}
		}
		&_item{
			display: flex;
			flex-direction: column;
			align-items: center;
			padding: 10px 0;
			.number-count{
				display: block;
				text-transform: uppercase;
				font-weight: 900;
				font-size: 56px;
				color: $dark;
			}
			.text{
				display: block;
				text-transform: uppercase;
				font-weight: 600;
				font-size: 24px;
				color: $dark;
			}
		}
	}
	.services{
		position: relative;
		background-color: #ffffff;
		.header-block{
			h3{
				color: $dark;
			}
		}
		&-block{
			&_item{
				position: relative;
				padding: 0;
				transition: $transition;
				&:before{
					content: "";
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					margin: 0 auto;
					height: 100%;
					background-color: rgba(0,0,0, .4);
					transition: all .3s linear 0s;
				}
				.image{
					height: 480px;
					img{
					    height: 100%;
    					object-fit: cover;
					}
				}
				.content-service{
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					margin: 0 auto;
					text-align: center;
					padding: 20px;
					color: #ffffff;
					height: 100%;
					display: flex;
				    flex-direction: column;
				    justify-content: space-between;
				    align-items: center;
				    .btn-gold{
				    	width: 200px;
				    }
					span{
						display: block;
						padding-bottom: 25px;
						color: #ffffff;
						font-weight: 600;
					}
					.header{
						display: block;
						font-size: 22px;
						line-height: 22px;
						font-weight: 600;
						padding-bottom: 40px;
					}
					.description{
						font-size: 14px;
						font-style: italic;
					}
					.price{
						font-weight: 600;
						font-size: 18px;
					}
				}
				&:hover{
					.price, .description{
						visibility: visible;
					}
				}
			}
		}
		.show-more{
			display: flex;
			justify-content: center;
			margin: 40px 0;
		}
	}
	.advantages{
		position: relative;
		background-color: #ffffff;
		.header-block{
			h3{
				color: $dark;
			}
		}
		&_item{
			margin-bottom: 30px;
			.img-svg{
				width: 70px;
				height: 70px;
				margin-bottom: 25px;
				object{
					width: 70px;
					height: 70px;
				}
			}
			.title_block{
				font-size: 22px;
				line-height: 22px;
				font-weight: 600;
				color: $gold;
				margin-bottom: 15px;
			}
		}
	}
	.sertificate{
		background: #eeeeee;
		.header-block{
			h3{
				color: $dark;
			}
		}
		&_block{
			justify-content: space-around;
		}
		&_item{
    		height: 360px;
    		margin-bottom: 30px;
    		img{
    			width: 100%;
    			height: inherit;
    			object-fit: contain;
    		}
    		@media screen and(max-width: 991px){
    			height: 235px;
    		}
		}
	}
	.our_works{
/*		background: url(../img/bg_work.jpg) no-repeat;
		background-size: cover;
		background-position: center;*/
		background: #ffffff;
		.header-block{
			h3{
				color: $dark;
			}

		}
		.fotorama{
			display: flex;
			justify-content: center;
		}
	}
}

.contacts{
	position: relative;
	background: url(../img/footer-bg.jpg) no-repeat;
	background-size: cover;
	.write_us{
		height: 480px;
	    padding: 30px 15px;
	    display: flex;
	    flex-direction: column;
	    justify-content: center;
	    align-items: center;
	    .social{
	    	padding-top: 40px;
	    	display: flex;
	    	a{	
	    		margin: 0 5px;
	    		background: #222;
	    		width: 40px;
	    		height: 40px;
	    		display: flex;
	    		justify-content: center;
	    		align-items: center;
    			border-radius: 30px;
	    		.fa{
	    			color: $gold;
	    			font-size: 24px;
	    		}
	    		&:hover{
	    			background: #fafafa;
		    		.fa{
		    			color: $dark;
		    		}
	    		}
	    	}
	    }
		&_text{
			display: block;
			font-weight: 600;
			font-size: 24px;
			margin-bottom: 50px;
			text-align: center;
		}
		&_form{
			display: flex;
			flex-direction: column;
			align-items: center;
			width: 100%;
			input{
				max-width: 320px;
				width: 100%;
				border-radius: 30px;
				outline: none;
				border: none;
				padding: 10px 20px;
				margin-bottom: 25px;
				border: 3px solid transparent;
				transition: $transition;
				font-weight: 600;
				&:hover{
					border: 3px solid $gold;
					transition: $transition;
				}
				&:focus{
					border: 3px solid $gold;
					transition: $transition;
					color: gray;
				}
			}
			button{
				border: none;
				outline: none;
				background: transparent;
				color: #fafafa;
				padding: 10px 20px;
				border-radius: 30px;
				border: 3px solid $gold;
				transition: $transition;
				font-weight: 600;
				&:hover,&:focus{
					background: $gold;
					color: $dark;
					transition: $transition;
				}
			}
		}
	}
	.map-block{
		padding: 0;
		background: url(../img/map-bg.png) no-repeat;
		background-size: cover;
		position: relative;
		iframe{
		    height: 100%;
			width: 100%;

		}
	}
	@media screen and (max-width: 991px){
		.header-block{
			padding-bottom: 0;
		}
		.map-block{
			height: 300px;
		}
	}
	.last_block{
		padding: 20px 0 0 0;
		a{
			color: $gold;
			padding: 0;
			&:hover{
				color: #ffffff;
			}
		}
		span{
			color: #ffffff;
			padding: 0;
		}
	}
}

.overlay{
	position: relative;
	&:before{
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		margin: 0 auto;
		background: rgba(0,0,0,.6);
		min-height: 100%;
		width: 100%;
		z-index: 900;
	}
}

.modal_write_us, .modal_success{
	position: fixed;
	z-index: 1000;
	top: calc(50% - 200px);
	left: 0;
	right: 0;
	margin: 0 auto;
	background: #333;
	display: none;
	/*display: flex;*/
	flex-direction: column;
	padding: 40px;
	align-items: center;
	justify-content: center;
	border-radius: 10px;
	max-width: 400px;
	width: 100%;
	h3{
		font-size: 24px;
		line-height: 28px;
		font-weight: 600;
		margin-bottom: 25px;
		text-align: center;
	}
	.write_us_form{
		display: flex;
		flex-direction: column;
		align-items: center;
		max-width: 320px;
		width: 100%;
		input{
			max-width: 320px;
			width: 100%;
			border-radius: 30px;
			outline: none;
			border: none;
			padding: 10px 20px;
			margin-bottom: 25px;
			border: 3px solid transparent;
			transition: $transition;
			font-weight: 600;
			&:hover{
				border: 3px solid $gold;
				transition: $transition;
			}
			&:focus{
				border: 3px solid $gold;
				transition: $transition;
				color: gray;
			}
		}
		button{
			border: none;
			outline: none;
			background: transparent;
			color: #fafafa;
			padding: 10px 20px;
			border-radius: 30px;
			border: 3px solid $gold;
			transition: $transition;
			font-weight: 600;
			&:hover,&:focus{
				background: $gold;
				color: $dark;
				transition: $transition;
			}
		}
	}
	@media screen and (max-width: 400px){
		padding: 40px 20px;
	}
}

.close-btn{
	height: 24px;
    width: 24px;
    position: absolute;
    top: 0;
    right: 0;
    margin: 12px;
    transition: $transition;
    cursor: pointer;
    .fa{
    	font-size: 24px;
    }
    &:hover{
    	transition: $transition;
    	.fa{
    		color: $gold;
    	}
    }
}
